section#customer {
    .contents-wrap {
        border-top: 5px solid rgba(62, 62, 62, 1);
        border-bottom: 2px solid rgba(112, 112, 112, 1);
        margin-top: 5rem;

        @media (min-width: 768px) {
            margin-top: 7rem;
        }

        .customer--center,
        .delivery--info {
            padding: 4rem 0;

            .box {
                max-width: 510px;
                margin: 0 auto;

                &.w-max-1000 {
                    max-width: 1000px;
                }

                h2 {
                    text-align: center;
                    font-weight: bolder;
                    font-size: 16px;

                    @media (min-width: 768px) {
                        font-size: 21px;
                    }
                }
            }
        }

        .customer--center {
            border-bottom: 1px solid rgba(62, 62, 62, 0.2);

            .comments {
                text-align: center;
                font-size: 13px;
                margin: 3rem 0;

                @media (min-width: 768px) {
                    font-size: 14px;
                    margin: 4rem 0;
                }

                p {
                    line-height: 1.4;

                    br {
                        display: block;

                        @media (min-width: 768px) {
                            display: none;
                        }
                    }

                    b {
                        font-weight: bolder;
                    }
                }
            }
        }
        .responsive--flex {
            position: relative;
            display: flex;
            flex-direction: column;

            @media (min-width: 768px) {
                flex-direction: row;
            }

            &.dark {
                background-color: rgba(62, 62, 62, 1);
                color: white;

                & > .flex {
                    &:first-child {
                        position: relative;

                        &:before {
                            content: '';
                            position: absolute;
                            bottom: 0;
                            left: 50%;
                            transform: translate(-50%, 0);
                            width: 75%;
                            height: 1px;
                            border-bottom: 1px solid rgba(110, 110, 110, 1);
                        }

                        @media (min-width: 768px) {
                            &:before {
                                display: none;
                            }

                            &:after {
                                content: '';
                                position: absolute;
                                top: 50%;
                                right: 0;
                                transform: translate(0, -50%);
                                width: 1px;
                                height: 60%;
                                border-right: 1px solid rgba(110, 110, 110, 1);
                            }
                        }

                        .phone-img {
                            width: 20px;
                            margin-right: 0.4rem;
                        }
                    }
                }
            }

            & > .flex {
                flex: 1;
                padding: 2rem 0;
            }

            .truck-img {
                width: 80px;
            }

            .clock-img {
                width: 60px;
                margin-bottom: 1rem;
                stroke-width: 1px;
            }

            .font-small {
                font-size: 13px;

                @media (min-width: 768px) {
                    font-size: 14px;
                }

                p {
                    line-height: 1.5;
                }
            }

            .cyon {
                color: rgba(128, 211, 142, 1);
            }

            .line {
                position: relative;
                width: 200px;
                border-top: 1px solid rgba(62, 62, 62, 0.1);
                margin: 0 auto;

                @media (min-width: 768px) {
                    width: 1px;
                    // height: 100%;
                    // max-width: 100%;
                    min-height: 100%;
                    border: 0;
                    margin: 0;

                    &:after {
                        content: '';
                        position: absolute;
                        top: 50%;
                        transform: translate(0, -50%);
                        width: 1px;
                        height: 60%;
                        border-right: 1px solid rgba(62, 62, 62, 0.1);
                    }
                }
            }
        }
    }
}
