main {
    position: relative;
    min-height: 100vh;
    padding-top: 57px;
    padding-bottom: 100px;

    @media (min-width: 768px) {
        padding-top: 110px;
        padding-bottom: 150px;
    }

    .container--1300 {
        width: 100%;
        max-width: 1300px;
        margin: 0 auto;
    }

    .container-default {
        width: 100%;
        max-width: $defaultMaxWidth;
        margin: 0 auto;
        padding: 5rem 1rem;

        @media (min-width: 768px) {
            padding: 5.5rem 1.5rem;
        }

        @media (min-width: 1366px) {
            padding: 6rem 2rem;
        }

        @media (min-width: 1440px) {
            padding: 7rem 2rem;
        }

        @media (min-width: 1680px) {
            padding: 7rem 0;
        }
    }

    .cyan {
        color: rgba(128, 211, 142, 1);
    }

    .caution,
    .information {
        margin-top: 0.3rem;
        font-size: 10px;

        @media (min-width: 768px) {
            font-size: 11px;
        }
    }

    .caution {
        color: rgba(190, 23, 23, 1);
    }

    .information {
        color: rgba(23, 81, 190, 1);
    }
}
