.category--empty-component {
    // min-height: 300px;
    // border: 1px solid red;
    position: relative;
    padding-top: 3rem;
    padding-bottom: 4rem;
    margin-top: 3rem;
    margin-bottom: 4rem;

    @media (min-width: 768px) {
        padding-top: 4rem;
        padding-bottom: 5rem;
        margin-top: 4rem;
        margin-bottom: 5rem;
    }
    @media (min-width: 1366px) {
        padding-top: 5rem;
        padding-bottom: 6rem;
        margin-top: 5rem;
        margin-bottom: 6rem;
    }
    @media (min-width: 1680px) {
        padding-top: 6rem;
        padding-bottom: 7rem;
        margin-top: 6rem;
        margin-bottom: 7rem;
    }

    .wrap {
        width: 100%;
        @include centered();
        text-align: center;
        font-size: 13px;

        @media (min-width: 768px) {
            font-size: 14px;
        }
        @media (min-width: 1440px) {
            font-size: 15px;
        }

        p {
            line-height: 1.5;
        }
    }
}
