hgroup.mypage--title-component {
    h2 {
        position: relative;

        span.swich {
            margin-left: 2rem;

            @media (min-width: 768px) {
                margin-left: 3rem;
            }

            a {
                font-size: 13px;

                @media (min-width: 768px) {
                    font-size: 14px;
                }

                &:first-child {
                    &::after {
                        content: '';
                        height: 100%;
                        border-right: 1px solid rgba(112, 112, 112, 0.5);
                        margin: 0 1rem;

                        @media (min-width: 768px) {
                            margin: 0 1.5rem;
                        }
                    }
                }
            }
        }
    }
}
