nav.mypage--navi-component {
    font-size: 13px;
    margin-bottom: 1rem;

    @media (min-width: 768px) {
        font-size: 14px;
        margin-bottom: 2rem;
    }
    @media (min-width: 1440px) {
        font-size: 15px;
        width: 150px;
        border-right: 1px solid rgba(62, 62, 62, 1);
    }

    ul {
        display: flex;
        flex-direction: column;
        justify-content: flex-start;

        li {
            margin-bottom: 1rem;

            @media (min-width: 768px) {
                margin-right: 1.5rem;
            }
            @media (min-width: 1366px) {
                margin-bottom: 2rem;
            }

            &.active {
                font-weight: bolder;
            }
        }
    }
}
