.my--information-component {
    display: flex;
    flex-direction: column;
    border-top: 5px solid rgba(62, 62, 62, 1);

    .box {
        display: flex;
        height: 60px;

        figure {
            @include flexCentered();
            width: 60px;
            height: 100%;
            border-right: 1px solid rgba(112, 112, 112, 1);
            border-bottom: 1px solid rgba(112, 112, 112, 1);

            img {
                width: 28px;
            }
        }

        .bundle {
            flex: 1;
            display: flex;
            flex-direction: column;
            justify-content: center;
            height: 100%;
            padding-left: 1rem;
            border-bottom: 1px solid rgba(112, 112, 112, 1);
            font-size: 14px;

            .title {
                margin-bottom: 0.6rem;
            }
        }
    }

    @media (min-width: 768px) {
        flex-direction: row;

        .box {
            flex: 1;
            height: 120px;

            &.point {
                max-width: 42%;
                figure {
                    display: none;
                }

                .bundle {
                    border-right: 1px solid rgba(112, 112, 112, 0);
                }
            }

            figure {
                width: 130px;

                img {
                    width: 40px;
                }
            }

            .bundle {
                padding-left: 1rem;
                font-size: 15px;
                border-right: 1px solid rgba(112, 112, 112, 1);
            }
        }
    }

    @media (min-width: 1024px) {
        .box {
            &.point {
                max-width: 45%;
            }
        }
    }

    @media (min-width: 1440px) {
        .box {
            figure {
                width: 150px;

                img {
                    width: 50px;
                }
            }

            .bundle {
                padding-left: 3rem;
            }
        }
    }
}
