article.suggest--container {
    background-color: white;
    padding-top: 6rem;

    @media (min-width: 768px) {
        padding-top: 10rem;
    }

    &.empty {
        .wrap {
            ul.products--wrap,
            .more {
                display: none;
            }

            .empty--box {
                display: block;

                figure {
                    border-top: 1px solid rgba(112, 112, 112, 1);
                    border-bottom: 1px solid rgba(112, 112, 112, 1);
                    text-align: center;
                    padding: 5rem 0 8rem;

                    @media (min-width: 768px) {
                        padding: 7rem 0 11rem;
                    }

                    img {
                        width: 100px;
                        opacity: 0.3;

                        @media (min-width: 768px) {
                            width: 120px;
                        }
                    }

                    figcaption {
                        font-size: 15px;

                        @media (min-width: 768px) {
                            margin-top: 0.5rem;
                            font-size: 18px;
                        }
                    }
                }
            }
        }
    }

    .wrap {
        width: 100%;
        max-width: $defaultMaxWidth;
        margin: 0 auto;

        hgroup.title {
            text-align: center;
            margin-bottom: 3rem;

            @media (min-width: 768px) {
                margin-bottom: 5rem;
            }

            h3 {
                font-size: 20px;
                font-weight: bold;

                @media (min-width: 768px) {
                    font-size: 35px;
                }
            }
        }

        ul.products--wrap {
            display: flex;
            flex-wrap: wrap;
            justify-content: center;
            // padding: 0 1rem;

            @media (min-width: 768px) {
                padding: 0 1.8rem;
                justify-content: space-between;
            }
            @media (min-width: 1024px) {
                padding: 0 2rem;
            }
            @media (min-width: 1680px) {
                padding: 0;
            }

            li {
                @media (max-width: 767px) {
                    &:nth-child(3),
                    &:last-child {
                        display: none;
                    }
                }

                @media (min-width: 1024px) and (max-width: 1365px) {
                    &:last-child {
                        display: none;
                    }
                }
            }
        }

        .more {
            @include flexCentered();
            padding: 0 0 3rem;

            @media (min-width: 768px) {
                padding: 1rem 0 3rem;
            }

            a {
                @include flexCentered();
                width: 116px;
                height: 29px;
                background-color: white;
                border: 1px solid rgba(112, 112, 112, 1);
                font-size: 13px;

                @media (min-width: 768px) {
                    width: 168px;
                    height: 33px;
                    font-size: 17px;
                }
            }
        }

        .empty--box {
            display: none;
        }
    }
}
