section#order,
section#order-complete {
    position: relative;

    .container {
        .prev--step {
            max-width: 100px;
            max-height: 36px;
            margin: 4rem 0 1rem;

            @media (min-width: 768px) {
                margin: 5rem 0 1rem;
            }

            &:before {
                content: '';
                display: inline-block;
                width: 8px;
                height: 8px;
                border: 1px solid rgba(62, 62, 62, 1);
                border-top: 1px solid rgba(62, 62, 62, 0);
                border-right: 1px solid rgba(62, 62, 62, 0);
                margin-right: 2px;
                transform: rotateZ(45deg);
            }
        }

        table.basket--table {
            margin: 0;
        }

        ul.order--board {
            margin: 4rem 0 1rem;

            @media (min-width: 768px) {
                margin: 5rem 0 1rem;
            }

            #order-flatpickr {
                display: flex;
                align-items: center;

                button {
                    border-radius: 50%;
                    padding: 0.3rem;
                    box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.2);

                    &:hover {
                        box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.4);
                    }
                }

                input[type='text'] {
                    border: 0;
                    border-bottom: 1px solid rgba(62, 62, 62, 1);
                    border-radius: 0;
                    margin-left: 1rem;
                    padding: 0.4rem 0.3rem;
                }
            }

            li {
                min-height: 60px;
                display: flex;
                border-bottom: 1px solid rgb(112, 112, 112);
                font-size: 12px;

                @media (min-width: 768px) {
                    font-size: 14px;
                }

                &:first-child {
                    min-height: 40px;
                    border-bottom: 5px solid rgb(62, 62, 62);
                    font-weight: bolder;
                    font-size: 14px;

                    @media (min-width: 768px) {
                        font-size: 15px;
                    }
                }

                .title {
                    width: 100%;
                    max-width: 90px;
                    padding: 23px 0;
                    display: flex;
                    // align-items: center;
                    background-color: rgb(244, 244, 244);
                    justify-content: center;
                    font-weight: bolder;

                    @media (min-width: 768px) {
                        max-width: 277px;
                        justify-content: flex-start;
                        padding-left: 2rem;
                    }
                }

                .description {
                    flex: 1;
                    display: flex;
                    align-items: center;
                    padding-left: 1rem;

                    @media (min-width: 768px) {
                        padding-left: 2rem;
                    }
                }

                .box {
                    width: 100%;
                    height: 250px;
                    overflow-y: scroll;
                    border: 1px solid rgba(62, 62, 62, 0.5);

                    @media (min-width: 768px) {
                        height: 280px;
                    }
                }
            }
        }
    }

    #payment-container {
        position: fixed;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 212px;
        background-color: rgba(62, 62, 62, 1);
        transition: 0.3s;

        &.compression {
            @media (max-width: 767px) {
                width: 60px;
                height: 60px;

                .wrap {
                    display: none;
                }
            }

            figure.toggle {
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                background-image: url('/assets/images/icons/toggle.svg');
                background-size: 40px;
            }
        }

        @media (min-width: 768px) {
            height: 105px;
        }
        @media (min-width: 1440px) {
            height: 88px;
        }

        figure.toggle {
            position: absolute;
            top: 0.5rem;
            left: 0.5rem;
            z-index: 2;
            width: 24px;
            height: 24px;
            cursor: pointer;
            background-image: url('/assets/images/icons/close-W.svg');
            background-size: 24px;
            background-repeat: no-repeat;
            background-position: center;
            // transition-delay: 0.2s;

            @media (min-width: 768px) {
                display: none;
            }
        }

        .wrap {
            position: relative;
            z-index: 1;
            color: white;
            width: 100%;
            max-width: 1400px;
            height: 100%;
            margin: 0 auto;

            @media (min-width: 768px) {
                padding: 0;
            }

            .contents--box {
                position: absolute;
                top: 0;
                right: 0;
                width: 50%;
                height: 100%;

                @media (min-width: 768px) {
                    width: 100%;
                }

                ul.order--payment-custom {
                    display: flex;
                    flex-direction: column;
                    font-size: 13px;
                    height: 100%;
                    padding: 0.5rem;

                    @media (min-width: 768px) {
                        flex-direction: row;
                        flex-wrap: wrap;
                        font-size: 15px;
                        padding: 0;
                    }

                    li {
                        position: relative;
                        height: 20%;
                        display: flex;
                        align-items: center;
                        justify-content: flex-end;

                        @media (min-width: 768px) {
                            height: auto;
                            width: 25%;
                            justify-content: center;
                        }

                        @media (min-width: 1440px) {
                            flex: 1;
                        }

                        &:last-child {
                            @media (min-width: 768px) {
                                justify-content: flex-end;
                                padding-right: 5%;
                                width: 100%;
                            }
                            @media (min-width: 1024px) {
                                padding-right: 6.8%;
                            }
                            @media (min-width: 1366px) {
                                padding-right: 7%;
                            }

                            @media (min-width: 1440px) {
                                justify-content: center;
                                width: auto;
                                padding: 0;
                            }
                        }

                        .minus,
                        .plus,
                        .equal {
                            position: absolute;
                            top: 50%;
                            left: 0;
                            transform: translate(0, -50%);
                            width: 17px;
                            height: 17px;

                            @media (min-width: 768px) {
                                transform: translate(-50%, -50%);
                            }
                        }

                        .minus,
                        .plus {
                            background-size: contain;
                            background-position: center;
                            background-repeat: no-repeat;
                            background-color: white;
                        }

                        .minus {
                            background-image: url('/assets/images/icons/minus.svg');
                        }

                        .plus {
                            background-image: url('/assets/images/icons/plus.svg');
                        }

                        .equal {
                            &:before {
                                content: '';
                                @include centered();
                                top: 30%;
                                width: 70%;
                                height: 1px;
                                background-color: white;
                            }

                            &:after {
                                content: '';
                                @include centered();
                                top: 75%;
                                width: 70%;
                                height: 1px;
                                background-color: white;
                            }
                        }

                        .box {
                            position: relative;

                            p {
                                display: inline-block;
                            }

                            b {
                                display: inline-block;
                                min-width: 90px;
                                text-align: right;

                                &.total {
                                    font-size: 19px;
                                    color: rgba(128, 211, 142, 1);

                                    @media (min-width: 1366px) {
                                        padding-left: 1rem;
                                        font-size: 28px;
                                    }
                                }

                                span {
                                    margin-right: 0.3rem;
                                }
                            }
                        }
                        .arithmetic {
                            left: 37%;
                            @include centeredY();
                            width: 17px;
                            height: 17px;
                            background-color: rgba(62, 62, 62, 1);

                            &.white {
                                background-color: white;
                            }

                            &.minus {
                                &:before {
                                    content: '';
                                    @include centered();
                                    width: 70%;
                                    height: 1px;
                                    background-color: white;
                                }
                            }

                            &.plus {
                                &:before {
                                    content: '';
                                    @include centered();
                                    width: 70%;
                                    height: 1px;
                                    background-color: white;
                                }

                                &:after {
                                    content: '';
                                    @include centered();
                                    width: 1px;
                                    height: 70%;
                                    background-color: white;
                                }
                            }

                            &.equal {
                                &:before {
                                    content: '';
                                    @include centered();
                                    top: 30%;
                                    width: 70%;
                                    height: 1px;
                                    background-color: rgb(62, 62, 62);
                                }

                                &:after {
                                    content: '';
                                    @include centered();
                                    top: 75%;
                                    width: 70%;
                                    height: 1px;
                                    background-color: rgb(62, 62, 62);
                                }
                            }
                        }

                        .payment--custom-btn {
                            margin: 0;
                            max-height: 40px;
                            max-width: 300px;
                        }
                    }
                }
            }

            .comments {
                position: absolute;
                left: 4.5%;
                bottom: 4.6%;
                font-size: 12px;

                @media (min-width: 768px) {
                    left: 3%;
                    bottom: 10%;
                }
                @media (min-width: 1024px) {
                    left: 5.3%;
                }
                @media (min-width: 1366px) {
                    left: 7%;
                }
                @media (min-width: 1440px) {
                    left: 4.8%;
                }

                p {
                    margin-bottom: 0.5rem;

                    @media (min-width: 768px) {
                        display: inline-block;
                        margin: 0;
                        margin-right: 1rem;

                        &:first-child {
                            display: block;
                            margin-bottom: 0.5rem;
                        }
                    }

                    @media (min-width: 1024px) {
                        &:first-child {
                            display: inline-block;
                            margin-bottom: 0rem;
                        }
                    }

                    &:last-child {
                        margin-bottom: 0;
                    }
                }

                @media (min-width: 768px) {
                }
            }
        }
    }
}
