section#clauses {
    .wrap {
        border-top: 5px solid rgba(62, 62, 62, 1);
        border-bottom: 2px solid rgba(112, 112, 112, 1);
        margin-top: 5rem;

        @media (min-width: 768px) {
            margin-top: 7rem;
        }

        .bundle {
            padding: 4rem 0;

            &:first-child {
                border-bottom: 1px solid rgba(62, 62, 62, 0.2);
            }

            h2 {
                text-align: center;
                font-weight: bolder;
                font-size: 16px;

                @media (min-width: 768px) {
                    font-size: 21px;
                }
            }

            .inside--scrolling-box {
                border: 1px solid rgba(62, 62, 62, 0.2);
                min-height: 500px;
                max-height: 500px;
                overflow-y: scroll;
                padding: 1rem;
                margin-top: 3rem;

                @media (min-width: 768px) {
                    margin-top: 4rem;
                }
            }
        }
    }
}
