section#refering {
    .contents--wrap {
        display: flex;
        flex-direction: column;
        margin-top: 2rem;

        @media (min-width: 768px) {
            margin-top: 3rem;
        }

        @media (min-width: 1366px) {
            flex-direction: row;
            margin-top: 4rem;
        }

        .box {
            position: relative;

            @media (min-width: 1366px) {
                padding-left: 2rem;
            }
        }
    }
}
