section#early {
    .sort--wrap {
        &.max-1000 {
            max-width: 1300px !important;
        }
    }

    .category--empty-component {
        border-top: 1px solid rgb(112, 112, 112);
        border-bottom: 1px solid rgb(112, 112, 112);
        height: 500px;
    }

    .products--wrap {
        max-width: 1300px;
        margin: 0 auto !important;

        .vertical--line {
            width: 100%;
            height: 50px;
            margin: 0.5rem 0 4rem;

            @media (min-width: 768px) {
                margin: 4rem 0;
            }
            @media (min-width: 1440px) {
                margin: 5rem 0;
            }

            &::before {
                content: '';
                display: inline-block;
                width: 50%;
                height: 100%;
                border-right: 1px solid rgba(112, 112, 112, 1);
            }
        }

        .choosed--check {
            display: flex;
            flex-direction: column;

            @media (min-width: 768px) {
                flex-direction: row;
                min-height: 150px;
            }
            @media (min-width: 1440px) {
                min-height: 266px;
            }

            .diet--box {
                position: relative;
                width: 100%;
                // max-width: 430px;
                display: flex;
                flex-direction: column;

                @media (min-width: 768px) {
                    width: 33%;
                    margin-right: 1rem;
                }

                .information {
                    position: absolute;
                    top: -22px;
                }

                hgroup {
                    color: white;
                    background-color: rgba(62, 62, 62, 1);
                    padding: 0.8rem 0.6rem;
                    margin-bottom: 0.5rem;
                    display: flex;
                    align-items: center;
                    justify-content: space-between;

                    h3 {
                        font-size: 14px;

                        @media (min-width: 768px) {
                            font-size: 15px;
                        }
                    }

                    span {
                        font-size: 12px;
                    }
                }

                ul {
                    flex: 1;
                    min-height: 120px;
                    border: 1px solid rgba(112, 112, 112, 1);
                    padding: 1rem;
                    display: flex;
                    flex-wrap: wrap;

                    li {
                        width: 50%;
                        height: 15px;
                        font-size: 13px;
                        margin-bottom: 0.7rem;

                        @media (min-width: 768px) {
                            font-size: 14px;
                        }

                        &:last-child {
                            margin: 0;
                        }

                        &:first-child {
                            order: 1;
                        }
                        &:nth-child(2) {
                            order: 3;
                        }
                        &:nth-child(3) {
                            order: 5;
                        }
                        &:nth-child(4) {
                            order: 7;
                        }
                        &:nth-child(5) {
                            order: 2;
                        }
                        &:nth-child(6) {
                            order: 4;
                        }
                        &:nth-child(7) {
                            order: 6;
                        }
                        &:nth-child(8) {
                            order: 8;
                        }

                        span {
                            &::before {
                                content: '-';
                                margin-right: 0.4rem;
                            }
                        }
                    }
                }
            }

            .choosed--box {
                position: relative;
                flex: 1;
                border: 1px solid rgba(112, 112, 112, 1);
                margin-top: 2rem;
                padding: 0.8rem 0.5rem;
                display: flex;
                flex-direction: column;

                @media (min-width: 768px) {
                    padding: 1rem 0.8rem;
                    margin-top: 0;
                }

                .information {
                    position: absolute;
                    bottom: -47px;
                    left: 0;

                    @media (min-width: 768px) {
                        bottom: -50px;
                    }
                }

                .week,
                .set,
                .month {
                    flex: 1;
                    display: flex;

                    .name {
                        height: 100%;
                        min-width: 70px;
                        max-width: 70px;
                        border-right: 1px solid rgba(112, 112, 112, 1);
                        padding-bottom: 3rem;
                        text-align: center;
                        font-size: 13px;

                        @media (min-width: 768px) {
                            max-width: 100px;
                            font-size: 14px;
                        }
                    }

                    .data {
                        position: relative;
                        padding-left: 1rem;

                        & > .caution {
                            margin-top: 0.7rem;
                        }
                    }
                }

                .week {
                    .data {
                        margin-top: -3px;

                        & > label {
                            margin-right: 0.5rem;

                            @media (min-width: 768px) {
                                margin-right: 0.7rem;
                            }

                            & > span {
                                padding-left: 0.3rem;
                            }
                        }
                    }
                }

                .set {
                    .data {
                        .count--component {
                            margin-top: -6px;
                            width: 85px;
                            height: 24px;
                        }
                    }
                }

                .month {
                    flex: 1.5;

                    .name {
                        padding-bottom: 4rem;
                    }

                    .data {
                        margin-top: -6px;

                        label.checkbox--component {
                            margin-right: 0.5rem;

                            @media (min-width: 768px) {
                                margin-right: 0.7rem;
                            }

                            & > span {
                                padding-left: 0.3rem;
                            }
                        }

                        .direct {
                            width: 100%;

                            .inputbox--component {
                                display: inline-block;
                                width: 60px;
                                height: 26px;
                                margin-left: 0.88rem;

                                input {
                                    height: 100%;
                                    // opacity: 0.3;
                                    transition: 0.3s;
                                }

                                & + span {
                                    padding-left: 0.5rem;
                                    font-size: 13px;
                                    // opacity: 0.3;
                                    transition: 0.3s;
                                }
                            }
                        }

                        .indirect {
                            margin-top: 0.7rem;

                            select.select--component {
                                text-align-last: left;
                                height: 28px;
                                border: 0;
                                margin-left: 1rem;
                                font-size: 14px;
                                background-position: 55% center;
                            }
                        }
                    }
                }
            }
        }

        .calculated--total-component {
            margin-top: 6rem;

            @media (min-width: 768px) {
                margin-top: 8rem;
            }
        }
    }
}
